@import "~slick-carousel/slick/slick.css";
@import "~slick-carousel/slick/slick-theme.css";

@font-face {
    font-family: 'hatton_semibold';
    src: url('./assets/fonts/Hatton-Semibold.woff2') format('woff2');
    font-weight: 600;
    font-style: normal;
    font-display: swap;
}

@font-face {
    font-family: 'hatton_regular';
    src: url('./assets/fonts/Hatton-Regular.woff2') format('woff2');
    font-weight: normal;
    font-style: normal;
    font-display: swap;
}



@font-face {
    font-family: 'opensans_regular';
    src: url('./assets/fonts/OpenSans-Regular.woff2') format('woff2');
    font-weight: normal;
    font-style: normal;
    font-display: swap;
}


html,
body,
div,
span,
applet,
object,
iframe,
h1,
h2,
h3,
h4,
h5,
h6,
blockquote,
pre,
a,
abbr,
acronym,
address,
big,
cite,
code,
del,
dfn,
em,
font,
ins,
kbd,
q,
s,
samp,
small,
strike,
strong,
sub,
sup,
tt,
var,
dl,
dt,
dd,
ol,
ul,
li,
fieldset,
form,
label,
legend,
table,
caption,
tbody,
tfoot,
thead,
tr,
th,
td {
    border: 0;
    font-style: normal;
    font-weight: normal;
    margin: 0;
    outline: 0;
    padding: 0;
    vertical-align: baseline;
}

body {
    font-size: 14px;
}

img {
    border: none;
    display: block;
}

input,
select {
    -webkit-appearance: none;
}

li {
    list-style: none;
}

a {
    text-decoration: none;
    display: block;
}

p {
    margin: 0;
}

.relative {
    position: relative;
}

.wp_centered {
    width: 87em;
    margin: 0 auto;
}

.wp_center {
    width: 86%;
    margin: 0 auto;
}

.wp_flexbox {
    display: flex;
    justify-content: space-between;
    flex-flow: row wrap;
    align-items: center;
}

.wp_flexbox-top {
    display: flex;
    justify-content: space-between;
    flex-flow: row wrap;
}

.wp_clear {
    clear: both;
}

.alignright {
    float: right;
    margin-left: 30px;
    margin-bottom: 15px;
}

.alignleft {
    float: left;
    margin-right: 30px;
    margin-bottom: 15px;
}

.aligncenter {
    margin: 0 auto;
    text-align: center;
    display: inherit;
}

* {
    box-sizing: border-box;
}

*:focus {
    outline: none;
}

header {
    width: 100%;
    z-index: 99;
    position: absolute;
    top: 60px;
}

section {
    width: 100%;
}

#menu-idiomas .sub-menu li:first-child {
    opacity: 0.5;
}

#menu-idiomas .current-menu-parent li.current_page_item,
#menu-idiomas .current-menu-parent li.current-menu-item {
    opacity: 0.5 !important;
}

#menu-idiomas .current-menu-parent li {
    opacity: 1 !important;
}

#menu-idiomas-1 .sub-menu li:first-child {
    opacity: 0.5;
}

#menu-idiomas-1 .current-menu-parent li.current_page_item,
#menu-idiomas-1 .current-menu-parent li.current-menu-item {
    opacity: 0.5 !important;
}

#menu-idiomas-1 .current-menu-parent li {
    opacity: 1 !important;
}

.wp_containerLogo {
    width: 15em;
}

.wp_containerLogo img {
    width: 14.5em;
    height: auto;
}

.wp_containerNav {
    display: flex;
    justify-content: space-between;
}

.field-nav {
    margin-right: 8em;
}

.field-nav ul {
    display: flex;
}

.field-nav ul li {
    margin-right: 5.8em;
}

.field-nav ul li a {
    font: 1.25em/1em hatton_semibold;
    color: #fff;
    transition: all 0.1s ease-in-out;
}

.field-nav ul li a:hover {
    color: #EF8000 !important;
}

.field-lang {
    display: flex;
}

.field-lang a {
    font: 1.25em/1em hatton_semibold;
    color: #fff;
    opacity: 1;
}

.field-lang i {
    font: 1.25em/1em hatton_semibold;
    color: #fff;
    margin: 0 4px;
}

.lang-active {
    opacity: 0.5 !important;
}

.field-lang-movil {
    display: none;
}

#wp_containerSlider-Home {
    width: 100%;
    height: 75em;
    position: relative;
}

#button-slider {
    display: flex;
    margin-bottom: 4em;
}

.slick-prev,
.slick-next {
    display: none !important;
}

#button-slider .slick_prev_arrow_add {
    width: 60px;
    height: 60px;
    border: none;
    font-size: 0;
    color: transparent;
    padding: 0;
    cursor: pointer;
    z-index: 99;
    background: url("./assets/webanterior/button-left.svg");
    background-size: contain;
}

#button-slider .slick_prev_arrow_add:hover {
    background: url("./assets/webanterior/button-left-hover.svg") !important;
    background-size: contain !important;
}

#button-slider .slick_next_arrow_add {
    width: 60px;
    height: 60px;
    border: none;
    font-size: 0;
    color: transparent;
    padding: 0;
    cursor: pointer;
    z-index: 99;
    background: url("./assets/webanterior/button-right.svg");
    background-size: contain;
    margin-left: 15px;
}

#button-slider .slick_next_arrow_add:hover {
    background: url("./assets/webanterior/button-right-hover.svg") !important;
    background-size: contain !important;
}

#numer-slider {
    display: flex;
    font: 1.3em/1em hatton_semibold;
    color: #fff;
    margin-bottom: 3.5em;
}

.fade-flash {
    animation: flashEffect 1s ease-in-out;
}

@keyframes flashEffect {
    0% {
        opacity: 0;
    }

    50% {
        opacity: 1;
        filter: brightness(1.3);
    }

    100% {
        opacity: 1;
        filter: brightness(1);
    }
}

.wp_columnSlider-Home {
    height: 75em;
    display: flex !important;
    align-items: flex-end !important;
}

.wp_layerSlider-Home {
    padding-left: 7%;
    position: relative;
    padding-right: 15%;
    background: url("./assets/webanterior/layer.svg");
    background-position: right top;
    background-size: 145%;
